<template>
    <div>
      <h1>JavaScript operator</h1>
      <ul>
        <li>
          <el-input style="width: 100px;" v-model="a[0]"></el-input>
          a[0] = {{a[0]}},a[0]++ = {{a[0]++}},a[0] = {{a[0]}},++a[0] = {{++a[0]}},a[0] = {{a[0]}},void a[0] = {{void a[0]}},
        </li>
        <li>
          <el-input style="width: 100px;" v-model="a[1]"></el-input>
          a[1] = {{a[1]}},a[1]-- = {{a[1]--}},a[1] = {{a[1]}},--a[1] = {{--a[1]}},a[1] = {{a[1]}}, typeof a[1] = {{typeof a[1]}},
        </li>
        <li>
          <el-input style="width: 100px;" v-model="a[2]"></el-input>
          a[2] = {{a[2]}},-a[2] = {{-a[2]}},a[2] = {{a[2]}},+a[2] = {{+a[2]}},a[2] = {{a[2]}},~a[2] = {{~a[2]}},a[2] = {{a[2]}},!a[2] = {{!a[2]}},
          <br/>
          a[2] = {{a[2]}},delete a[2] = {{delete a[2]}},
        </li>
        <li>
          <p style="color: red;">空值数组，赋值会相互干扰，具体原因不明</p>
          <p style="color: #2276d2;">问题已解决，每次改变或赋值时均会调用operator，例如++，所以值每次都有改变</p>
        </li>
        <li>
          <el-input style="width: 100px;" v-model="b"></el-input>
          <!--b = {{b}},b+b = {{b+b}},移位符?  <p> "11" < "3"的值为true  </p>-->
        </li>
        <li>
          <el-button @click="evalTest">evalTest</el-button>
        </li>
        <li><p style="color: red;">链式调用 foreach map P156</p></li>
        <li>
          <el-button @click="everyTest">everyTest</el-button>
          <el-button @click="aTest">aTest</el-button>
        </li>
        <li><p>闭包 词法作用域</p></li>
        {{a}}
        <li>
          <el-button>a[3]+1={{a[3]}}</el-button>
        </li>
        <li>
          <el-input style="width: 100px;"  v-model="a[5]">{{a[5]}}</el-input>
          <el-input style="width: 100px;"  v-model="a[6]">{{a[6]}}</el-input>
        </li>
      </ul>
    </div>
</template>

<script>
export default {
  name: 'operator',
  data () {
    return {
      a: [],
      b: ''
    }
  },
  methods: {
    evalTest () {
      this.a[3] = 1
      // var geval = NaN
      console.log(this.a[3])
      // for (var i = 0; i < 10; this.a[i++] = 0) {}
    },
    everyTest () {
      var a = [1, 2, 3, 4, 5]
      var b = a.every(function (x) {
        return x % 2 === 0
      })
      var c = a.some(isNaN)
      console.log(b)
      console.log(c)
    },
    aTest () {
      this.a[3] += 1
      console.log(this.a)
    },
    create () {
      var result = []
      for (var i = 0; i < 10; i++) {
        result[i] = (function (num) {
          return num
        })(i)
      }
      return result
    },
    show () {
      // var name = 'T$$$'
      var i = 0
      return (function inner () {
        console.log(i)
        i++
      }(1))
    }
  }
}
</script>

<style scoped>

</style>



